/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { PhidgetChannel } from '../Phidget';
import { Channel } from '../Channel';
import { ErrorCode, ChannelClass } from '../Enumerations.gen';
import * as Enum from '../Enumerations.gen';
import { PhidgetError } from '../PhidgetError';
import { BridgePacket, PUNK } from '../BridgePacket';
import { BP } from '../BridgePackets.gen';
import { logEventException } from '../Logging';

/** @internal */
// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface AccelerometerData {
	dataInterval: number,
	timestamp: number,
	maxDataInterval: number,
	minDataRate: number,
	maxDataRate: number,
	maxAcceleration: readonly [number, number, number],
	maxAccelerationChangeTrigger: number,
	minDataInterval: number,
	minAcceleration: readonly [number, number, number],
	minAccelerationChangeTrigger: number,
	acceleration: readonly [number, number, number],
	accelerationChangeTrigger: number,
	axisCount: number,
	precision: Enum.SpatialPrecision | PUNK.ENUM,
	heatingEnabled: number,
}

abstract class AccelerometerBase extends PhidgetChannel {
	/** @internal */
	data: AccelerometerData;
	/**
	 * **AccelerationChange** event
	 *  * `acceleration` - The acceleration values
	 *  * `timestamp` - The timestamp value
	 * ---
	 * The most recent acceleration values the channel has measured will be reported in this event, which occurs when the `dataInterval` has elapsed.
	 * 
	 * *   If a `accelerationChangeTrigger` has been set to a non-zero value, the `AccelerationChange` event will not occur until the acceleration has changed by at least the `accelerationChangeTrigger` value.
	 */
	onAccelerationChange: ((acceleration: readonly [number, number, number], timestamp: number) => void) | null = null;
	/** @internal */
	_gotAccelerationChangeErrorEvent?: boolean;

	/**
	 * The Accelerometer class is used to gather acceleration data from Phidget accelerometer boards. Phidget accelerometers usually have multiple sensors, each oriented in a different axis, so multiple dimensions of acceleration can be recorded.
	 * 
	 * If the Phidget you're using also has a gyroscope and a magnetometer, you may want to use the Spatial class in order to get all of the data at the same time, in a single event.
	 * @public
	 */
	constructor();
	/** @internal */
	constructor(ch?: Channel);
	constructor(ch?: Channel) {
		super(ch);
		this._class = ChannelClass.ACCELEROMETER;
		this.name = "Accelerometer";
		this.data = this._initData();
	}

	/** @internal */
	_bridgeInput(bp: BridgePacket) {

		switch(bp.vpkt) {
		case BP.SETCHANGETRIGGER:
			this.data.accelerationChangeTrigger = bp.entries[0].v as number;
			this._FIREPropertyChange('AccelerationChangeTrigger', bp);
			break;
		case BP.SETDATAINTERVAL:
			if (bp.entryCount > 1)
				this.data.dataInterval = bp.entries[1].v as number;
			else
				this.data.dataInterval = bp.entries[0].v as number;
			this._FIREPropertyChange('DataInterval', bp);
			this._FIREPropertyChange('DataRate', bp);
			break;
		case BP.SETHEATINGENABLED:
			this.data.heatingEnabled = bp.entries[0].v as number;
			this._FIREPropertyChange('HeatingEnabled', bp);
			break;
		case BP.SETSPATIALPRECISION:
			this.data.precision = bp.entries[0].v as Enum.SpatialPrecision;
			this._FIREPropertyChange('Precision', bp);
			break;
		case BP.ACCELERATIONCHANGE: {
			this.data.acceleration = bp.entries[0].v as [number, number, number];
			this.data.timestamp = bp.entries[1].v as number;
			if (this._isAttachedDone && this.onAccelerationChange) {
				try {
					this.onAccelerationChange(this.data.acceleration, this.data.timestamp);
				} catch (err) { logEventException(err); }
			}
			break;
		}
		default:
		// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
			throw new PhidgetError(ErrorCode.INVALID_PACKET, "Unsupported bridge packet: 0x" + bp.vpkt!.toString(16));
		}
	}

	/** @internal */
	_initData(): AccelerometerData {
		return {
			dataInterval: PUNK.DBL,
			timestamp: PUNK.DBL,
			maxDataInterval: PUNK.UINT32,
			minDataRate: PUNK.DBL,
			maxDataRate: PUNK.DBL,
			maxAcceleration: [PUNK.DBL, PUNK.DBL, PUNK.DBL],
			maxAccelerationChangeTrigger: PUNK.DBL,
			minDataInterval: PUNK.UINT32,
			minAcceleration: [PUNK.DBL, PUNK.DBL, PUNK.DBL],
			minAccelerationChangeTrigger: PUNK.DBL,
			acceleration: [PUNK.DBL, PUNK.DBL, PUNK.DBL],
			accelerationChangeTrigger: PUNK.DBL,
			axisCount: PUNK.INT32,
			precision: PUNK.ENUM,
			heatingEnabled: PUNK.BOOL,
		}
	}

	/** @internal */
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	_initAfterOpen() {}

	/** @internal */
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	async _setDefaults() {}

	/** @internal */
	_hasInitialState() {


		return true;
	}

	/** @internal */
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	_fireInitialEvents() {

	}

	/**
	 * The most recent acceleration value that the channel has reported.
	 * 
	 * *   This value will always be between `minAcceleration` and `maxAcceleration`.
	 * @throws {@link PhidgetError}
	 */
	get acceleration() { return this.getAcceleration(); }
	/**
	 * The minimum value the `AccelerationChange` event will report.
	 * @throws {@link PhidgetError}
	 */
	get minAcceleration() { return this.getMinAcceleration(); }
	/**
	 * The maximum value the `AccelerationChange` event will report.
	 * @throws {@link PhidgetError}
	 */
	get maxAcceleration() { return this.getMaxAcceleration(); }
	/**
	 * The channel will not issue a `AccelerationChange` event until the acceleration value has changed by the amount specified by the `accelerationChangeTrigger`.
	 * 
	 * *   Setting the `accelerationChangeTrigger` to 0 will result in the channel firing events every `dataInterval`. This is useful for applications that implement their own data filtering
	 * @throws {@link PhidgetError}
	 */
	get accelerationChangeTrigger() { return this.getAccelerationChangeTrigger(); }
	/**
	 * The minimum value that `accelerationChangeTrigger` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get minAccelerationChangeTrigger() { return this.getMinAccelerationChangeTrigger(); }
	/**
	 * The maximum value that `accelerationChangeTrigger` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get maxAccelerationChangeTrigger() { return this.getMaxAccelerationChangeTrigger(); }
	/**
	 * The number of axes the channel can measure acceleration on.
	 * 
	 * *   See your device's User Guide for more information about the number of axes and their orientation.
	 * @throws {@link PhidgetError}
	 */
	get axisCount() { return this.getAxisCount(); }
	/**
	 * The `dataInterval` is the time that must elapse before the channel will fire another `AccelerationChange` event.
	 * 
	 * *   The data interval is bounded by `minDataInterval` and `maxDataInterval`.
	 * *   The timing between `AccelerationChange` events can also be affected by the `accelerationChangeTrigger`.
	 * @throws {@link PhidgetError}
	 */
	get dataInterval() { return this.getDataInterval(); }
	/**
	 * The minimum value that `dataInterval` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get minDataInterval() { return this.getMinDataInterval(); }
	/**
	 * The maximum value that `dataInterval` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get maxDataInterval() { return this.getMaxDataInterval(); }
	/**
	 * The `dataRate` is the frequency of events from the device.
	 * 
	 * *   The data rate is bounded by `minDataRate` and `maxDataRate`.
	 * *   Changing `dataRate` will change the channel's `dataInterval` to a corresponding value, rounded to the nearest integer number of milliseconds.
	 * *   The timing between events can also affected by the change trigger.
	 * @throws {@link PhidgetError}
	 */
	get dataRate() { return this.getDataRate(); }
	/**
	 * The minimum value that `dataRate` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get minDataRate() { return this.getMinDataRate(); }
	/**
	 * The maximum value that `dataRate` can be set to.
	 * @throws {@link PhidgetError}
	 */
	get maxDataRate() { return this.getMaxDataRate(); }
	/**
	 * Set to TRUE to enable the temperature stabilization feature of this device. This enables onboard heating elements to bring the board up to a known temperature to minimize ambient temerature effects on the sensor's reading. You can leave this setting FALSE to conserve power consumption.  
	 *   
	 * If you enable heating, it is strongly recommended to keep the board in its enclosure to keep it insulated from moving air.  
	 *   
	 * This property is shared by any and all spatial-related objects on this device (Accelerometer, Gyroscope, Magnetometer, Spatial)
	 * @throws {@link PhidgetError}
	 */
	get heatingEnabled() { return this.getHeatingEnabled(); }
	/**
	 * Selects between high/low precision sensing chips.
	 * @throws {@link PhidgetError}
	 * @internal
	 */
	get precision() { return this.getPrecision(); }
	/**
	 * The most recent timestamp value that the channel has reported. This is an extremely accurate time measurement streamed from the device.
	 * 
	 * *   If your application requires a time measurement, you should use this value over a local software timestamp.
	 * @throws {@link PhidgetError}
	 */
	get timestamp() { return this.getTimestamp(); }

	/**
	 * The most recent acceleration value that the channel has reported.
	 * 
	 * *   This value will always be between `minAcceleration` and `maxAcceleration`.
	 * @returns The acceleration values
	 * @throws {@link PhidgetError}
	 */
	getAcceleration(): readonly [number, number, number] {
		this._assertOpen();

		if (this.data.acceleration.includes(PUNK.DBL))
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.acceleration);
	}

	/**
	 * The minimum value the `AccelerationChange` event will report.
	 * @returns The minimum acceleration value
	 * @throws {@link PhidgetError}
	 */
	getMinAcceleration(): readonly [number, number, number] {
		this._assertOpen();

		if (this.data.minAcceleration.includes(PUNK.DBL))
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.minAcceleration);
	}

	/**
	 * The maximum value the `AccelerationChange` event will report.
	 * @returns The maximum acceleration values
	 * @throws {@link PhidgetError}
	 */
	getMaxAcceleration(): readonly [number, number, number] {
		this._assertOpen();

		if (this.data.maxAcceleration.includes(PUNK.DBL))
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.maxAcceleration);
	}

	/**
	 * The channel will not issue a `AccelerationChange` event until the acceleration value has changed by the amount specified by the `accelerationChangeTrigger`.
	 * 
	 * *   Setting the `accelerationChangeTrigger` to 0 will result in the channel firing events every `dataInterval`. This is useful for applications that implement their own data filtering
	 * @returns The change trigger value
	 * @throws {@link PhidgetError}
	 */
	getAccelerationChangeTrigger(): number {
		this._assertOpen();

		if (this.data.accelerationChangeTrigger === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.accelerationChangeTrigger);
	}

	/**
	 * The channel will not issue a `AccelerationChange` event until the acceleration value has changed by the amount specified by the `accelerationChangeTrigger`.
	 * 
	 * *   Setting the `accelerationChangeTrigger` to 0 will result in the channel firing events every `dataInterval`. This is useful for applications that implement their own data filtering
	 * @throws {@link PhidgetError}
	 * @param accelerationChangeTrigger - The change trigger value
	 */
	async setAccelerationChangeTrigger(accelerationChangeTrigger: number): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "g", value: accelerationChangeTrigger });
		await bp.send(this._ch, BP.SETCHANGETRIGGER);
	}

	/**
	 * The minimum value that `accelerationChangeTrigger` can be set to.
	 * @returns The minimum change trigger value
	 * @throws {@link PhidgetError}
	 */
	getMinAccelerationChangeTrigger(): number {
		this._assertOpen();

		if (this.data.minAccelerationChangeTrigger === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.minAccelerationChangeTrigger);
	}

	/**
	 * The maximum value that `accelerationChangeTrigger` can be set to.
	 * @returns The maximum change trigger value
	 * @throws {@link PhidgetError}
	 */
	getMaxAccelerationChangeTrigger(): number {
		this._assertOpen();

		if (this.data.maxAccelerationChangeTrigger === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.maxAccelerationChangeTrigger);
	}

	/**
	 * The number of axes the channel can measure acceleration on.
	 * 
	 * *   See your device's User Guide for more information about the number of axes and their orientation.
	 * @returns The number of axes
	 * @throws {@link PhidgetError}
	 */
	getAxisCount(): number {
		this._assertOpen();

		if (this.data.axisCount === PUNK.INT32)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.axisCount);
	}

	/**
	 * The `dataInterval` is the time that must elapse before the channel will fire another `AccelerationChange` event.
	 * 
	 * *   The data interval is bounded by `minDataInterval` and `maxDataInterval`.
	 * *   The timing between `AccelerationChange` events can also be affected by the `accelerationChangeTrigger`.
	 * @returns The data interval value
	 * @throws {@link PhidgetError}
	 */
	getDataInterval(): number {
		this._assertOpen();

		if (this.data.dataInterval === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return this.data.dataInterval;
	}

	/**
	 * The `dataInterval` is the time that must elapse before the channel will fire another `AccelerationChange` event.
	 * 
	 * *   The data interval is bounded by `minDataInterval` and `maxDataInterval`.
	 * *   The timing between `AccelerationChange` events can also be affected by the `accelerationChangeTrigger`.
	 * @throws {@link PhidgetError}
	 * @param dataInterval - The data interval value
	 */
	async setDataInterval(dataInterval: number): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "u", value: dataInterval });
		await bp.send(this._ch, BP.SETDATAINTERVAL);
	}

	/**
	 * The minimum value that `dataInterval` can be set to.
	 * @returns The minimum data interval value
	 * @throws {@link PhidgetError}
	 */
	getMinDataInterval(): number {
		this._assertOpen();

		if (this.data.minDataInterval === PUNK.UINT32)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.minDataInterval);
	}

	/**
	 * The maximum value that `dataInterval` can be set to.
	 * @returns The data interval value
	 * @throws {@link PhidgetError}
	 */
	getMaxDataInterval(): number {
		this._assertOpen();

		if (this.data.maxDataInterval === PUNK.UINT32)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.maxDataInterval);
	}

	/**
	 * The `dataRate` is the frequency of events from the device.
	 * 
	 * *   The data rate is bounded by `minDataRate` and `maxDataRate`.
	 * *   Changing `dataRate` will change the channel's `dataInterval` to a corresponding value, rounded to the nearest integer number of milliseconds.
	 * *   The timing between events can also affected by the change trigger.
	 * @returns The data rate for the channel
	 * @throws {@link PhidgetError}
	 */
	getDataRate(): number {
		this._assertOpen();

		if (this.data.dataInterval === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (1000.0 / this.data.dataInterval);
	}

	/**
	 * The `dataRate` is the frequency of events from the device.
	 * 
	 * *   The data rate is bounded by `minDataRate` and `maxDataRate`.
	 * *   Changing `dataRate` will change the channel's `dataInterval` to a corresponding value, rounded to the nearest integer number of milliseconds.
	 * *   The timing between events can also affected by the change trigger.
	 * @throws {@link PhidgetError}
	 * @param dataRate - The data rate for the channel
	 */
	async setDataRate(dataRate: number): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "u", value: Math.round(1000.0 / dataRate) });
		bp.set({ name: "1", type: "g", value: (1000.0 / dataRate) });
		await bp.send(this._ch, BP.SETDATAINTERVAL);
	}

	/**
	 * The minimum value that `dataRate` can be set to.
	 * @returns The data rate value
	 * @throws {@link PhidgetError}
	 */
	getMinDataRate(): number {
		this._assertOpen();

		if (this.data.minDataRate === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.minDataRate);
	}

	/**
	 * The maximum value that `dataRate` can be set to.
	 * @returns The data rate value
	 * @throws {@link PhidgetError}
	 */
	getMaxDataRate(): number {
		this._assertOpen();

		if (this.data.maxDataRate === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.maxDataRate);
	}

	/**
	 * Set to TRUE to enable the temperature stabilization feature of this device. This enables onboard heating elements to bring the board up to a known temperature to minimize ambient temerature effects on the sensor's reading. You can leave this setting FALSE to conserve power consumption.  
	 *   
	 * If you enable heating, it is strongly recommended to keep the board in its enclosure to keep it insulated from moving air.  
	 *   
	 * This property is shared by any and all spatial-related objects on this device (Accelerometer, Gyroscope, Magnetometer, Spatial)
	 * @returns Whether self-heating temperature stabilization is enabled
	 * @throws {@link PhidgetError}
	 */
	getHeatingEnabled(): boolean {
		this._assertOpen();

		if (this.data.heatingEnabled === PUNK.BOOL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (!!this.data.heatingEnabled);
	}

	/**
	 * Set to TRUE to enable the temperature stabilization feature of this device. This enables onboard heating elements to bring the board up to a known temperature to minimize ambient temerature effects on the sensor's reading. You can leave this setting FALSE to conserve power consumption.  
	 *   
	 * If you enable heating, it is strongly recommended to keep the board in its enclosure to keep it insulated from moving air.  
	 *   
	 * This property is shared by any and all spatial-related objects on this device (Accelerometer, Gyroscope, Magnetometer, Spatial)
	 * @throws {@link PhidgetError}
	 * @param heatingEnabled - Whether self-heating temperature stabilization is enabled
	 */
	async setHeatingEnabled(heatingEnabled: boolean): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "d", value: (heatingEnabled ? 1 : 0) });
		await bp.send(this._ch, BP.SETHEATINGENABLED);
	}

	/**
	 * Selects between high/low precision sensing chips.
	 * @returns The sensor precision value
	 * @throws {@link PhidgetError}
	 * @internal
	 */
	getPrecision(): Enum.SpatialPrecision {
		this._assertOpen();

		if (this.data.precision === PUNK.ENUM)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.precision);
	}

	/**
	 * Selects between high/low precision sensing chips.
	 * @throws {@link PhidgetError}
	 * @param precision - The sensor precision value
	 * @internal
	 */
	async setPrecision(precision: Enum.SpatialPrecision): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "d", value: precision });
		await bp.send(this._ch, BP.SETSPATIALPRECISION);
	}

	/**
	 * The most recent timestamp value that the channel has reported. This is an extremely accurate time measurement streamed from the device.
	 * 
	 * *   If your application requires a time measurement, you should use this value over a local software timestamp.
	 * @returns The timestamp value
	 * @throws {@link PhidgetError}
	 */
	getTimestamp(): number {
		this._assertOpen();

		if (this.data.timestamp === PUNK.DBL)
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.timestamp);
	}

}
export { AccelerometerBase };
