	async setBackEMFSensingState(backEMFSensingState: boolean): Promise<void> {
		this._assertOpen();

		const bp = new BridgePacket();
		bp.set({ name: "0", type: "d", value: (backEMFSensingState ? 1 : 0) });
		await bp.send(this._ch, BP.SETBACKEMFSENSINGSTATE);
	}

