	getMinAcceleration(): readonly [number, number, number] {
		this._assertOpen();

		if (this.data.minAcceleration.includes(PUNK.DBL))
			throw new PhidgetError(ErrorCode.UNKNOWN_VALUE);

		return (this.data.minAcceleration);
	}

